@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);

* {

margin: 0;

padding: 0;
box-sizing: border-box;
}


body{
background-color:skyblue;
background-image: url(/random-quote-generator/static/media/lakhubaa.1dd4c27f.jpeg);
background-repeat:no-repeat;
  /* background-size:contain; */
  background-position:center;
  background-size: 100%;
  
  height: 90%;
  margin: 30%;
    font-family: 'Dancing Script', cursive;
    /* color: white; */
}


#quote-box {
    display:flex;  
    border-radius: 5px;
    justify-content: flex-start;
    align-content: flex-end;
    flex-direction: column;

}

#text {
    color: black;
    display: flex;
    font-size: 2rem;
    margin-bottom: 1.5rem;
    

}

#author {
    color: black;
    display: flex;
    justify-content: flex-end;
    font-size: 1.2rem;
}

#new-quote {
    background:white ;
    border: none;
    color:black;
    padding:.7rem 1rem;
    border-radius: 5px;
    font-family: 'Dancing Script', cursive;
    font-weight: bold;
    font-size: 1rem;
    cursor:pointer;
    outline: none;
}

#new-quote:hover {
    opacity: 0.6;
}
